const wordNormalize = (word: string, count: number, sex = 'm') => {
    let end: Array<string> = [];
    if(sex === 'm') {
        end = ['', '', '', '', '', '', '', '', '', '', '']
    }
    if(sex === 'v') {
        end = ['', '', '', '', '', '', '', '', '', '', '']
    }
    
    return `${word}${end[count % 10]}`
}

export default wordNormalize;